* {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

.header {
  height: 100vh;
  width: 100vw;
  background-image: url(/assets/images/cover.jpg);
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center; }

.header-text {
  color: white;
  /*height: 50vh;
    width: 50vw;*/
  border: solid blue 2px; }

.header-title {
  text-transform: uppercase;
  width: 290px;
  height: 110px;
  border: red solid 2px;
  text-align: center;
  font-size: 42px;
  /*margin-top: 284px;
    margin-bottom: 174px;
    margin-left: 15px;
    margin-right: 15px;*/ }

.header-subtitle {
  text-transform: initial;
  text-align: center;
  margin-top: 10px;
  border: solid 2px white; }

.header-button {
  width: 50px;
  height: 50px;
  background-color: white;
  border-radius: 30px;
  margin-bottom: 53px;
  margin-top: 45px; }
